.label {
    color: grey;
    position: absolute;
    line-height: 46px;
    width: 100%;
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    top: 0;
    left: 0;
    pointer-events: none;
    stroke: grey;
    transition: all 100ms;
}

.tagItems {
    width: 100%;
}

.tagList {
    position: absolute;
    top: 110%;
    width: 100%;
    height: 200px;
    overflow-y: scroll;
}

.filter .active {
    transform: translate(-25%, -28%) scale(0.5);
    stroke: #1976d2;
    color: #1976d2;
}

.filter .icon {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: 20px;
}

.active .icon {
    margin-left: 25px;
}

.filter input {
    width: 100%;
    font-size: 1em;
    border: none;
    padding: 20px;
}

.filter .cross {
    position: absolute;
    right: 10px;
    margin: auto;
    top: 25%;
    bottom: 25%;
}

.filter .select {
    width: 100%;
}